import React, { Component } from 'react';
import localforage from 'localforage';

class DrivingLogForm extends Component {
	constructor() {
		super();
		this.state = {
			distance: 0,
			distanceUnits: 'miles',
			fuelAmountUsed: 0,
			fuelUsageUnits: 'gallons',
			fuelPayAmount: 0,
			fuelPayAmountCurrency: 'USD',
			fuelType: 'gasoline',
      carDescription: '',
      tripDescription: ''
		};
	}

	handleDistanceChange = (event) => {
		this.setState({distance: event.target.value});
	}

	handleDistanceUnitsChange = (event) => {
		let selectedDistanceUnits = event.target.value || 'miles';
		if (selectedDistanceUnits === 'kilometers') {
			this.setState({
				distanceUnits: selectedDistanceUnits,
				fuelUsageUnits: 'liters',
				fuelPayAmountCurrency: 'EUR'
			});
		} else {
			this.setState({distanceUnits: selectedDistanceUnits});
		}
	}

	handleFuelAmountUsedChange = (event) => {
		this.setState({fuelAmountUsed: event.target.value});
	}	

	handleFuelUsageUnitsChange = (event) => {
		this.setState({fuelUsageUnits: event.target.value});
	}

	handlePayAmountCurrencyChange = (event) => {
		this.setState({fuelPayAmountCurrency: event.target.value});
	}

	handleFuelPayAmountChange = (event) => {
		this.setState({fuelPayAmount: event.target.value});
	}	

	handleFuelTypeChange = (event) => {
		this.setState({fuelType: event.target.value});
	}

	handleCarDescriptionChange = (event) => {
		this.setState({carDescription: event.target.value});
  }
  
	handleTripDescriptionChange = (event) => {
		this.setState({tripDescription: event.target.value});
	}  

  handleSubmit = (event) =>  {
		event.preventDefault();
    let today = new Date();

    // friggin react / javascript / promises and its weird scoping!!!
    // 
    let that = this;    

    // Get input
    let entry = {};
    entry["timestamp"] = today.toISOString();
    entry["distance"] = this.state.distance;
    entry["fuelAmountUsed"] = this.state.fuelAmountUsed;
    entry["fuelUsageUnits"] = this.state.fuelUsageUnits;
    entry["fuelPayAmount"] = this.state.fuelPayAmount;
    entry["fuelPayAmountCurrency"] = this.state.fuelPayAmountCurrency;
    entry["fuelType"] = this.state.fuelType;
    entry["carDescription"] = this.state.carDescription;
    entry["tripDescription"] = this.state.tripDescription;

    // Set up localforage to use IndexedDB
    localforage.config({
      driver      : localforage.INDEXEDDB, // Force INDEXEDDB; same as using setDriver()
      name        : 'autoUseLog',
      version     : 1.0,
      size        : 4980736, // Size of database, in bytes. WebSQL-only for now.
      storeName   : 'auto_use_log', // Should be alphanumeric, with underscores.
      description : 'log auto usage'
    });   
  
    // insert data into local DB
    localforage.setItem(today.getTime().toString(), entry).then(function () {
      return localforage.getItem('key');
    }).then(function (value) {
      // we got our value
      //showTab('list-entries');
      that.props.onAdd();
    }).catch(function (err) {
      // we got an error
      alert("adding item failed");
    }); 
  }
  
	
	render() {
		return (
			<form onSubmit={this.handleSubmit}>

				<div className="columns is-gapless">
					<div className="column" />

					<div className="column">			

						<div className="field has-addons">
							<label className="label">
								How far did you drive?
							</label>
						</div>
						<div className="field has-addons">
							<div className="control is-expanded">
								<input
									className="input"
									name="distance"
									type="number" 
									step="any"
									defaultValue="{this.state.distance}" 
									onChange={this.handleDistanceChange} />
							</div>
							<p className="control">
								<span className="select">

									<select value={this.state.distanceUnits} 
										onChange={this.handleDistanceUnitsChange}>
											<option value="miles">miles</option>
											<option value="kilometers">kilometers</option>
									</select>	
								</span>
							</p>
						</div>

						<div className="field has-addons">
							<label className="label">
								How much fuel did you use?
							</label>
						</div>
						<div className="field has-addons">
							<div className="control is-expanded">
								<input
									className="input"
									name="fuelAmountUsed"
									type="number"
									step="any" 
									defaultValue="{this.state.fuelAmountUsed}" 
									onChange={this.handleFuelAmountUsedChange} />					
							</div>
							<p className="control">
								<span className="select">

									<select value={this.state.fuelUsageUnits} 
									onChange={this.handleFuelUsageUnitsChange}>
										<option value="gallons">gallons</option>
										<option value="liters">liters</option>
									</select>
								</span>
							</p>
						</div>				

						<div className="field has-addons">
							<label className="label">
								How much did you pay for fuel?
							</label>
						</div>
						<div className="field has-addons">
							<p className="control">
								<span className="select">

									<select value={this.state.fuelPayAmountCurrency} 
										onChange={this.handlePayAmountCurrencyChange}>
											<option value="USD">$</option>
											<option value="EUR">€</option>
											<option value="GBP">£</option>
									</select>	
								</span>
							</p>
							<div className="control is-expanded">
								<input
									className="input"
									name="fuelPayAmount"
									type="number"
									step="any" 
									defaultValue="{this.state.fuelPayAmount}" 
									onChange={this.handleFuelPayAmountChange}/>				
							</div>
						</div>

						<div className="field has-addons">
							<label className="label">
								Fuel Type?
							</label>
						</div>
						<div className="field has-addons">
							<p className="control">
								<span className="select">
									<select value={this.state.fuelType} 
										onChange={this.handleFuelTypeChange}>
											<option value="gasoline">gasoline</option>
											<option value="diesel">diesel</option>
											<option value="other">other</option>
									</select>
								</span>
							</p>	
						</div>						

						<div className="field has-addons">
							<label className="label">
								Car Description:
							</label>
						</div>
						<div className="field has-addons">
							<div className="control is-expanded">
								<input
									className="input"
									name="carDescription"
									type="text" 
									defaultValue="" 
									onChange={this.handleCarDescriptionChange} />		
							</div>
						</div>

						<div className="field has-addons">
							<label className="label">
								Trip Description:
							</label>
						</div>
						<div className="field has-addons">
							<div className="control is-expanded">
								<input
									className="input"
									name="tripDescription"
									type="text" 
									defaultValue="" 
									onChange={this.handleTripDescriptionChange} />		
							</div>
						</div>            

						<div className="field is-grouped">
							<p className="control">
								<a type="submit" 
									onClick={this.handleSubmit} 
									className="button is-success">
									Add
								</a>
							</p>
							<p className="control">
								<a className="button is-danger">
									Clear
								</a>
							</p>
						</div>						

						{/* <input type="submit" value="Submit" /> */}
					</div>

					<div className="column" />
				</div>				
			</form>  
		);
	}
}

export default DrivingLogForm;


