import React, { Component } from 'react';
import localforage from 'localforage';

class AutoUseLogItems extends Component {
	constructor() {
    super();
		this.state = {
			items: []
		};
	}

  // WARNING! This implementation might be killing battery life
  // console.log is going crazy if i turn on logs!
	render() {
    let entries = [];

    // friggin react / javascript / promises and its weird scoping!!!
    // 
    let that = this;    

    // Set up localforage to use IndexedDB
    localforage.config({
      driver      : localforage.INDEXEDDB, // Force INDEXEDDB; same as using setDriver()
      name        : 'autoUseLog',
      version     : 1.0,
      size        : 4980736, // Size of database, in bytes. WebSQL-only for now.
      storeName   : 'auto_use_log', // Should be alphanumeric, with underscores.
      description : 'log auto usage'
    });     

    localforage.iterate(function(value, key, iterationNumber) {
      // Resulting key/value pair -- this callback
      // will be executed for every item in the
      // database.
      //console.log([key, JSON.stringify(value)], iterationNumber);
      //add it to the array
      let distanceDriven = value["distance"];
      let fuelUsed = value["fuelAmountUsed"]; 
      let litersPer100km = (fuelUsed / distanceDriven) * 100;  
      value["fuelEfficiency"] = `${litersPer100km.toFixed(2)} liters/100km`;   
      entries.push(value);
    }).then(function() {
      //console.log('Iteration has completed');
      //console.log('Sorting list with date descending and redisplaying...');
      // sort by date descending
      let sortedItems = entries.sort((a,b) => {
        let aTimestamp = (new Date(a["timestamp"])).getTime();
        let bTimestamp = (new Date(b["timestamp"])).getTime();
        return bTimestamp - aTimestamp;
      });
      //console.log(`sortedItems = ${JSON.stringify(sortedItems)}`)
      that.setState({items: sortedItems});
    }).catch(function(err) {
      // This code runs if there were any errors
      console.log(err);
    });

		return (
      <table id="entry-items" className="table">
        <thead>
          <tr>
            <th>Date</th>
            <th>Trip Details</th>
            <th>Fuel Efficiency</th>
          </tr>
        </thead>
        <tbody>
          {
            this.state.items.map((item) =>             
              <tr key={new Date(item["timestamp"]).getTime()}>
                <td>{ (new Date(item["timestamp"])).toLocaleString() }</td>
                <td>{ item["tripDescription"] }</td>
                <td className="has-text-right">{ item["fuelEfficiency"] }</td>
              </tr>                
            )
          }
        </tbody>
      </table>
		);
	}
}

export default AutoUseLogItems;


